.container {
  /* --container-max-width: 1200px; */
  --container-padding: 15px;
  --gutter: 30px;
  width: 100%;
  max-width: var(--container-max-width);
  margin: 0 auto;
  padding: 0 var(--container-padding);
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin-left: calc(var(--container-padding) * -1);
  margin-right: calc(var(--container-padding) * -1);
}

.col {
  padding: 0 calc(var(--gutter) / 2);
}

.-cols-1 .col {
  flex: 0 0 calc((100% / 12) * calc(12 / 1));
}

.-cols-2 .col {
  flex: 0 0 calc((100% / 12) * calc(12 / 2));
}

.-cols-3 .col {
  flex: 0 0 calc((100% / 12) * calc(12 / 3));
}

.-cols-4 .col {
  flex: 0 0 calc((100% / 12) * calc(12 / 4));
}

.-cols-5 .col {
  flex: 0 0 calc((100% / 12) * calc(12 / 5));
}

.-cols-6 .col {
  flex: 0 0 calc((100% / 12) * calc(12 / 6));
}

.-cols-7 .col {
  flex: 0 0 calc((100% / 12) * calc(12 / 7));
}

.-cols-8 .col {
  flex: 0 0 calc((100% / 12) * calc(12 / 8));
}

.-cols-9 .col {
  flex: 0 0 calc((100% / 12) * calc(12 / 9));
}

.-cols-10 .col {
  flex: 0 0 calc((100% / 12) * calc(12 / 10));
}

.-cols-11 .col {
  flex: 0 0 calc((100% / 12) * calc(12 / 11));
}

.-cols-12 .col {
  flex: 0 0 calc((100% / 12) * calc(12 / 12));
}

:root {
  --color-black: #000;
  --color-white: #fff;
  --color-dark: #151515;
  --quill-gray: #e5e5e3;
  --color-gray: #858585;
  --color-tundora: #454545;
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  overflow-anchor: none;
  margin: 0;
  font-family: "Jost", sans-serif;
  font-weight: 500;
  color: var(--color-dark);
  background-color: var(--color-white);
}

img {
  max-width: 100%;
}

a {
  text-decoration: none;
  color: inherit;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

.is-outer {
  position: relative !important;
  left: -9999px !important;
}

.site-container {
  overflow: hidden;
}

.is-hidden {
  display: none !important;
}

.for-mobile {
  display: none;
}

.error {
  border: 2px #ff4019 solid !important;
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.container {
  margin: 0 auto;
  padding: 0 40px;
  max-width: 1920px;
  width: 100%;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.main-link {
  position: relative;
  font-size: 15px;
  color: var(--color-dark);
}

.main-link::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: -5px;
  width: 100%;
  height: 2px;
  background-color: currentColor;
  opacity: 0;
  transition: opacity 0.5s linear;
}

.main-link:hover::before {
  opacity: 1;
  transition: opacity 0.5s linear;
}

.main-link--white {
  color: var(--color-white);
}

.container-narrow {
  max-width: 1406px;
}

.to-top {
  position: fixed;
  right: 2%;
  bottom: 5%;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border-radius: 100%;
  padding: 14px 18px;
  width: 48px;
  height: 48px;
  box-shadow: 0 5px 30px rgba(0, 0, 0, 0.1);
  background-color: var(--color-white);
  transition: transform 0.3s;
}

.to-top:hover {
  transform: rotate(360deg);
  transition: transform 0.3s;
}

@font-face {
  font-family: "Jost";
  src: url("../fonts/Jost-Medium.woff2") format("woff2");
  font-weight: 500;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Jost";
  src: url("../fonts/Jost-Bold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Oswald";
  src: url("../fonts/Oswald-Bold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}

.nav__list {
  display: flex;
}

.nav__item:not(:last-child) {
  margin-right: 20px;
}

.header {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 100;
  width: 100%;
}

.header__container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding-top: 36px;
  padding-bottom: 36px;
}

.header__shop-nav {
  margin-left: 250px;
}

.logo {
  display: inline-block;
  width: 117px;
  height: 22px;
}

.logo .logo img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  fill: var(--color-dark);
}

.shop-nav {
  display: flex;
  align-items: center;
}

.shop-nav__item:not(:last-child) {
  margin-right: 25px;
}

.shop-nav__link {
  display: inline-block;
  width: 16px;
  height: 16px;
}

.shop-nav__link--user {
  background: url("../../img/serch-user.png") no-repeat center/cover;
}

.cart {
  height: auto;
  width: auto;
}

.cart__icon {
  display: inline-block;
  width: 16px;
  height: 16px;
  background: url("../../img/serch-bag.png") no-repeat center/cover;
}

.cart__quantity {
  color: var(--color-white);
  background-color: var(--color-dark);
  height: 16px;
  display: inline-flex;
  padding: 0 5px;
  align-items: center;
  border-radius: 25px;
  font-weight: 700;
  font-size: 10px;
}

.serch-form__btn {
  display: inline-block;
  width: 16px;
  height: 16px;
  background: url("../../img/search-space.png") no-repeat center/cover;
}

.catalog__header {
  padding-top: 38px;
}

.hero {
  position: relative;
  height: 100vh;
  height: 1080px;
  min-height: 700px;
  background-color: var(--quill-gray);
  overflow: hidden;
}

.hero::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.05);
}

.hero__container {
  height: 100%;
}

.hero__container picture {
  position: absolute;
  right: 277px;
  bottom: 0;
  display: block;
  max-height: 90%;
}

.hero__title {
  margin: 0;
  color: var(--color-dark);
  font-size: 72px;
  font-weight: 700;
  text-align: left;
  max-width: 540px;
  margin-bottom: 40px;
}

.hero__descr {
  font-size: 18px;
  font-weight: 500;
  text-align: left;
}

.hero__img {
  display: block;
  object-fit: cover;
}

.hero__content {
  padding: 0 260px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  height: 100%;
}

.banner-slider {
  overflow: hidden;
  width: 100%;
  height: 100%;
}

.banner-slider .swiper-slide {
  height: 100%;
}

.banner-slider .swiper-pagination-bullet {
  margin: 0 12px !important;
  width: 6px;
  height: 6px;
  background-color: var(--color-white);
  opacity: 0.5;
}

.banner-slider .swiper-pagination-bullet-active {
  position: relative;
  opacity: 1;
  background-color: var(--color-dark);
}

.banner-slider .swiper-pagination-bullet-active::after {
  content: '';
  position: absolute;
  z-index: -1;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 20px;
  height: 20px;
  background-color: var(--color-white);
  border-radius: 50%;
}

.macketing {
  position: absolute;
  bottom: 50px;
  left: 40px;
  z-index: 2;
  display: flex;
  width: 280px;
  padding: 10px;
  box-shadow: 0 5px 30px rgba(0, 0, 0, 0.1);
  background-color: var(--color-white);
  transform: translateX(-150vw);
  transition: transform 0.3s ease-in-out;
}

.macketing--visible {
  transform: translateX(0);
  transition: transform 0.3s ease-in-out;
}

.macketing__img {
  width: 70px;
  height: 110px;
  background: url(../img/marketing.jpg) no-repeat center/cover;
  margin-right: 10px;
}

.macketing__descr {
  color: var(--color-gray);
  font-family: 'Jost';
  font-size: 11px;
  font-weight: 500;
  text-align: left;
  margin-bottom: 7px;
}

.macketing__title {
  font-size: 15px;
  font-weight: 500;
  text-align: left;
  color: var(--color-dark);
  max-width: 140px;
  margin-bottom: 9px;
}

.macketing__when-from {
  max-width: 114px;
  color: var(--color-gray);
  font-family: "Jost";
  font-size: 11px;
  font-weight: 400;
  text-align: left;
}

.macketing__btn {
  position: absolute;
  right: 5px;
  background-color: transparent;
  outline: none;
  border: none;
}

.banners__container {
  padding-top: 50px;
  padding-bottom: 50px;
}

.banners__list {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.banners__item {
  flex-shrink: 0;
  flex-basis: 33%;
}

.banners__item:not(:last-child) {
  margin-right: 0.6%;
}

.banners__article {
  padding: 54px 60px;
  background-color: #e9e4df;
  padding-right: 45%;
  min-height: 290px;
  background-repeat: no-repeat;
  background-position: right;
  background-size: contain;
}

.banners__article--midseason {
  background-image: url("../../img/model-1.png");
  background-position: 85%;
  background-color: #cfd1d0;
}

.banners__article--summer {
  background-image: url("../../img/model-2.jpg");
}

.banners__article--acss {
  background-image: url("../../img/model-3.png");
  background-position: right;
  background-color: #eeeeec;
}

.banners__title {
  margin: 0;
  margin-bottom: 20px;
  font-size: 36px;
  font-weight: 500;
}

.main-products__container {
  padding-top: 55px;
  padding-bottom: 55px;
}

.main-products__nav {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 45px;
}

.main-products__item {
  margin: 0 20px;
}

.main-products__btn {
  font-size: 24px;
  outline: none;
}

.main-products__btn--current::before {
  opacity: 1;
  transform: opacity 0.5s;
}

.main-products__btn-center {
  text-align: center;
}

.main-products__more {
  margin-top: 90px;
  display: inline-flex;
  flex-direction: column;
  justify-content: center;
  color: var(--color-gray);
  font-size: 13px;
  text-align: center;
  outline: none;
}

.main-products__more span {
  margin-bottom: 14px;
}

.main-products__more svg {
  width: 12px;
  height: 21px;
  margin: 0 auto;
}

.products-grid {
  display: flex;
  flex-wrap: wrap;
}

.products-grid__item {
  margin-right: 30px;
  margin-bottom: 80px;
}

.products-grid__item:nth-child(5n) {
  margin-right: 0;
}

.products-grid:nth-last-child(-n+5) {
  margin-bottom: 0;
}

.product {
  position: relative;
}

.product__image {
  margin-bottom: 20px;
}

.product__image img {
  object-fit: cover;
  max-height: 100%;
}

.product__prop {
  display: inline-block;
  color: #111;
  padding-left: 15px;
  margin-bottom: 5px;
  font-size: 10px;
  font-weight: 700;
  text-transform: uppercase;
  background-position: left center;
  background-repeat: no-repeat;
  padding-left: 20px;
}

.product .new {
  background-image: url("../img/bolt.svg");
}

.product .hot {
  background-image: url("../img/kz-fire.png");
}

.product .best {
  background-image: url("../img/kz-number-one.png");
}

.product .rate {
  background-image: url("../../img/star-line.png");
}

.product__title {
  color: var(--color-tundora);
  font-size: 13px;
  font-weight: 500;
  margin-bottom: 8px;
}

.product__title a::before {
  content: '';
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
}

.product__price {
  color: var(--color-dark);
  font-size: 13px;
  font-weight: 700;
}

.clothes-banners {
  padding-top: 45px;
}

.clothes-banners__container {
  display: flex;
  justify-content: space-between;
  padding: 0;
  max-width: 100%;
}

.clothes-banners__link {
  position: relative;
  flex-basis: 50%;
  padding: 50px;
  min-height: 420px;
  background-color: var(--color-dark);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: var(--color-white);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.clothes-banners__link::before {
  content: '';
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  top: 0;
  background-color: rgba(215, 8, 8, 0.1);
}

.clothes-banners__link--man {
  background-image: url(../../img/man.jpg);
}

.clothes-banners__link--women {
  background-image: url(../../img/clothes.jpg);
}

.clothes-banners__title {
  position: relative;
  z-index: 1;
  margin: 0;
  font-size: 48px;
  font-weight: 500;
  text-align: center;
  margin-bottom: 20px;
}

.clothes-banners__text {
  position: relative;
  z-index: 1;
}

