  margin-bottom: 38px;
  color: var(--color-tundora);
  font-size: 13px;
  font-weight: 500;
}

.subscribe-form__btn {
  position: absolute;
  right: 10px;
  bottom: 10px;
  z-index: 2;
  color: var(--color-dark);
  font-size: 13px;
}

.subscribe-form__field {
  display: block;
  width: 100%;
  border-bottom: 2px solid var(--color-dark);
  padding-bottom: 10px;
}

.subscribe-form__field input {
  padding-right: 80px;
  width: 100%;
  border: none;
  outline: none;
  color: var(--color-dark);
}

.subscribe-form__field input::placeholder {
  opacity: 0.5;
  color: var(--color-tundora);
  font-size: 13px;
}

.social {
  display: flex;
  justify-content: space-between;
}

.social__item:not(:last-child) {
  margin-right: 30px;
}

.social__link {
  display: block;
  width: 16px;
  height: 16px;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.social__link--tw {
  background-image: url(../../img/twitter.png);
}

.social__link--fb {
  background-image: url(../../img/facebook.png);
}

.social__link--inst {
  background-image: url(../../img/instagram.png);
}

.social__link--pin {
  background-image: url(../../img/pinterest.png);
}

.hero-catalog {
  padding-top: 130px;
}

.hero-catalog__slider {
  overflow: hidden;
  width: 100%;
  background-color: #f8f8f8;
}

.hero-catalog__container {
  position: relative;
}

.catalog-slide {
  padding: 108px;
}

.catalog-slide__title {
  margin: 0;
  max-width: 375px;
  color: var(--color-dark);
  font-size: 36px;
  font-weight: 700;
  margin-bottom: 15px;
}

.catalog-slide__descr {
  margin: 0;
  max-width: 390px;
  color: var(--color-tundora);
  font-size: 15px;
  line-height: 28px;
  margin-bottom: 40px;
}

.catalog-slide__link {
  border: 1px solid var(--color-dark);
  display: inline-block;
  font-size: 13px;
  text-align: center;
  padding: 16px 43px;
}

.catalog-slide__link--dark {
  color: var(--color-white);
  background-color: var(--color-dark);
}

.catalog-slide__link--light {
  margin-left: 15px;
  color: var(--color-dark);
  background-color: transparent;
}

.catalog-slide__btns {
  display: flex;
  align-items: center;
}

.catalog-slide__img {
  position: absolute;
  bottom: 0;
  right: 19%;
}

.hero-prev-btn {
  display: inline-block;
  width: 48px;
  height: 48px;
  position: absolute;
  top: 50%;
  left: 5%;
  z-index: 5;
  outline: none;
  transform: translateY(-50%);
  border: 2px solid var(--color-dark);
  transition: all 0.3s linear;
  background-color: transparent;
}

.hero-prev-btn svg {
  width: 32px;
  height: 24px;
}

.hero-prev-btn:hover {
  border: 2px solid var(--color-white);
  background-color: var(--color-dark);
}

.hero-prev-btn:hover svg {
  fill: var(--color-white);
}

.hero-next-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 48px;
  height: 48px;
  position: absolute;
  z-index: 5;
  top: 50%;
  right: 5%;
  outline: none;
  transform: translateY(-50%);
  border: 2px solid var(--color-dark);
  transition: all 0.3s linear;
  background-color: transparent;
}

.hero-next-btn svg {
  transform: rotate(180deg);
  width: 32px;
  height: 24px;
}

.hero-next-btn:hover {
  border: 2px solid var(--color-white);
  background-color: var(--color-dark);
}

.hero-next-btn:hover svg {
  fill: var(--color-white);
}

.breadcrumbs__list {
  padding-top: 36px;
  padding-bottom: 26px;
  display: flex;
  align-items: center;
  border-bottom: 1px solid #eee;
}

.breadcrumbs__item {
  position: relative;
}

.breadcrumbs__item:not(:last-child) {
  margin-right: 35px;
}

.breadcrumbs__item:not(:last-child)::after {
  content: '/';
  position: absolute;
  right: -20px;
  top: 50%;
  color: var(--color-gray);
  font-size: 10px;
  transform: translateY(-50%);
}

.breadcrumbs__link {
  color: var(--color-gray);
  font-size: 10px;
  text-transform: uppercase;
  font-weight: 500;
}

.breadcrumbs__link--current {
  color: var(--color-dark);
}

.catalog-fillters {
  width: 309px;
  padding-top: 27px;
  margin-bottom: 18px;
  margin-right: 30px;
}

.catalog-content {
  min-height: 500px;
}

.catalog-content .container {
  display: flex;
}

.hide-filtres {
  display: none;
  padding-left: 24px;
  margin-bottom: 10px;
  color: var(--color-dark);
  font-size: 10px;
  font-weight: 700;
  text-transform: uppercase;
  background: url("../../img/close.png") no-repeat left center;
}

.catalog-filter {
  border-top: 1px solid #eee;
  padding-right: 66px;
}

.catalog-filter__top {
  cursor: pointer;
  padding-top: 34px;
  padding-bottom: 34px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  user-select: none;
}

.catalog-filter__list {
  columns: 2;
}

.catalog-filter__item {
  margin-bottom: 14px;
}

.catalog-filter__item:nth-child(5n) {
  margin-bottom: 0;
}

.catalog-filter__title {
  margin: 0;
  display: inline-block;
  color: var(--color-dark);
  font-size: 13px;
  font-weight: 700;
  text-transform: uppercase;
}

.catalog-filter .quantity {
  color: var(--color-white);
  background-color: var(--color-dark);
  height: 16px;
  display: inline-flex;
  padding: 0 5px;
  align-items: center;
  border-radius: 25px;
  font-weight: 700;
  font-size: 10px;
}

.catalog-filter__quantity {
  min-width: 16px;
  margin-left: 5px;
  transform: translateY(-1px);
}

.catalog-filter__caption {
  display: flex;
  align-items: center;
}

.catalog-filter__toggle {
  position: relative;
  width: 12px;
  height: 13px;
}

.catalog-filter__toggle::before {
  content: '';
  position: absolute;
  left: 50%;
  top: 0;
  height: 100%;
  width: 2px;
  transform: translateX(-50%);
  background-color: #aaaaaa;
}

.catalog-filter__toggle::after {
  content: '';
  position: absolute;
  left: 0;
  top: 50%;
  height: 2px;
  width: 100%;
  transform: translateY(-50%);
  background-color: #aaaaaa;
}

.catalog-filter__bottom {
  display: none;
}

.catalog-filter--open .catalog-filter__bottom {
  display: block;
}

.catalog-filter--open .catalog-filter__toggle::before {
  display: none;
}

.catalog-filter:last-child {
  border-bottom: 1px solid #eee;
}

.custom-checkbox {
  display: inline-block;
  cursor: pointer;
  user-select: none;
}

.custom-checkbox__text {
  position: relative;
  padding-left: 15px;
  color: var(--color-gray);
  font-size: 13px;
}

.custom-checkbox__text::before {
  content: '';
  position: absolute;
  left: 0;
  top: 3px;
  width: 11px;
  height: 11px;
  border: 2px solid #aaa;
}

.custom-checkbox__text::after {
  content: '\2713';
  position: absolute;
  left: 2px;
  top: 1px;
  color: #aaa;
}

.custom-checkbox__input:checked + span {
  color: var(--color-dark);
}

.custom-checkbox__input:checked + span::before {
  border-color: var(--color-dark);
}

.custom-checkbox__input:checked + span::after {
  color: var(--color-dark);
}

.catalog-grid {
  padding-top: 35px;
  width: 987px;
  font-weight: 700;
  font-size: 10px;
  text-transform: uppercase;
}

.catalog-grid__content {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 70px;
}

.catalog-grid__item {
  margin-bottom: 79px;
}

[data-grid-columns="3"] .catalog-grid__item {
  width: 31.3%;
  margin-right: 0%;
}

[data-grid-columns="3"] .catalog-grid__item:not(:nth-child(3n)) {
  margin-right: 3%;
}

[data-grid-columns="3"] .catalog-grid__item:nth-last-child(-n+3) {
  margin-bottom: 0;
}

[data-grid-columns="4"] .catalog-grid__item {
  width: 23%;
  margin-right: 0%;
}

[data-grid-columns="4"] .catalog-grid__item:not(:nth-child(4n)) {
  margin-right: 2%;
}

[data-grid-columns="4"] .catalog-grid__item:nth-last-child(-n+4) {
  margin-bottom: 0;
}

[data-grid-columns="5"] .catalog-grid__item {
  width: 19%;
  margin-right: 0%;
}

[data-grid-columns="5"] .catalog-grid__item:not(:nth-child(5n)) {
  margin-right: 1%;
}

[data-grid-columns="5"] .catalog-grid__item:nth-last-child(-n+5) {
  margin-bottom: 0;
}

.catalog-props {
  margin-bottom: 20px;
}

.catalog-props__top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 26px;
}

.catalog-columns {
  display: flex;
  align-items: center;
}

.catalog-columns__list {
  display: flex;
  align-items: center;
}

.catalog-columns__text {
  margin-right: 5px;
}

.catalog-columns__item:not(:last-child) {
  margin-right: 5px;
}

.catalog-columns__btn {
  padding: 0 5px;
  font-size: 10px;
  color: var(--color-gray);
  outline: none;
}

.catalog-columns__btn:hover {
  color: var(--color-dark);
}

.catalog-choice {
  display: none;
  margin-bottom: 20px;
  font-size: 0;
}

.catalog-choice__item {
  padding: 5px 12px;
  margin-right: 5px;
  padding-right: 28px;
  color: var(--color-tundora);
  font-size: 13px;
  font-weight: 500;
  text-align: center;
  border-radius: 12px;
  background-color: #eee;
  outline: none;
  transition: all 0.3s linear;
}

.catalog-choice__item svg {
  margin-left: 5px;
  width: 8px;
  height: 9px;
  fill: var(--color-tundora);
  pointer-events: none;
}

.catalog-choice__item:hover {
  background-color: var(--color-dark);
  color: var(--color-white);
}

.catalog-choice__item:hover svg {
  fill: var(--color-white);
}

.catalog-choice__clean {
  position: relative;
  color: var(--color-dark);
  font-size: 13px;
  font-weight: 500;
}

.catalog-choice__clean::before {
  content: "";
  position: absolute;
  left: 0;
  bottom: -5px;
  width: 100%;
  height: 2px;
  background-color: var(--color-dark);
  opacity: 1;
  transition: opacity 0.5s linear;
}

.custom-select {
  position: relative;
  min-width: 115px;
}

.custom-select__dropdown {
  display: none;
  position: absolute;
  left: 0;
  top: calc(100% + 2px);
  z-index: 20;
  background-color: #fff;
  width: 100%;
  padding: 10px;
}

.custom-select__item {
  transition: color 0.3s linear;
}

.custom-select__item:not(:last-child) {
  margin-bottom: 10px;
  cursor: pointer;
}

.custom-select__item:hover {
  color: var(--color-tundora);
  transition: color 0.3s linear;
}

.custom-select__top {
  position: relative;
  color: var(--color-dark);
  font-size: 10px;
  font-weight: 700;
  text-transform: uppercase;
  border-bottom: 1px solid var(--color-gray);
  padding-bottom: 14px;
  padding-right: 32px;
}

.custom-select__top::after {
  content: '';
  cursor: pointer;
  display: block;
  width: 20px;
  height: 16px;
  background: url("../img/free-back.svg") no-repeat center/cover;
  position: absolute;
  top: 50%;
  right: 0;
  transform: translateY(-100%) rotate(270deg);
  transition: transform 0.3s linear;
}

.custom-select--open .custom-select__top::after {
  transform: translateY(-100%) rotate(90deg);
  transition: transform 0.3s linear;
}

.custom-select--open .custom-select__dropdown {
  display: block;
}

.pagination {
  display: flex;
  align-items: center;
  justify-content: center;
}

.pagination__item:not(:last-child) {
  margin-right: 8px;
}

.pagination__link {
  padding: 0 16px;
  height: 40px;
  display: inline-flex;
  align-items: center;
  border: 2px solid #eee;
  color: var(--color-gray);
  font-size: 10px;
  font-weight: 700;
  text-align: center;
  text-transform: uppercase;
  transition: all 0.3s ease-in-out;
}

.pagination__link svg {
  margin-left: 3px;
  width: 7px;
  height: 16px;
  fill: var(--color-gray);
  transform: rotate(180deg);
  transition: all 0.3s ease-in-out;
}

.pagination__link:hover {
  background-color: var(--color-dark);
  color: var(--color-white);
  border: 2px solid var(--color-dark);
  transition: all 0.3s ease-in-out;
}

.pagination__link:hover svg {
  transition: all 0.3s ease-in-out;
  fill: var(--color-white);
}

.pagination__link--current {
  border: 2px solid var(--color-dark);
  background-color: var(--color-dark);
  color: var(--color-white);
}
